"use client";

import React, { useState, useEffect, useRef } from "react";
import { FiSend, FiPaperclip } from "react-icons/fi";
import { supabase } from "../../database/supabaseClient";

export const UserChat: React.FC = () => {
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState<any[]>([]);
  const [attachments, setAttachments] = useState<File[]>([]);
  const [sending, setSending] = useState(false);
  const [currentUserId, setCurrentUserId] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const chatEndRef = useRef<HTMLDivElement>(null);

  const ADMIN_ID = "6c77d859-1af1-418e-b0e0-0d3cc4e80fad";

  // Load current user id
  useEffect(() => {
    supabase.auth.getSession().then(({ data }) => {
      const uid = data.session?.user?.id ?? null;
      console.log("üë§ User ID:", uid);
      setCurrentUserId(uid);
    });
  }, []);

  // Fetch ALL messages that involve this user (sent OR received), oldest ‚Üí newest
  useEffect(() => {
    const loadMessages = async () => {
      if (!currentUserId) return;
      console.log("üì¶ Fetching messages for:", currentUserId);

      const { data, error } = await supabase
        .from("dm_messages")
        .select("*")
        .or(`sender_id.eq.${currentUserId},recipient_id.eq.${currentUserId}`)
        .order("created_at", { ascending: true });

      if (error) {
        console.error("‚ùå Fetch failed:", error.message);
        return;
      }
      console.log(`‚úÖ Loaded ${data.length} messages`);
      setMessages(data);
    };

    loadMessages();
  }, [currentUserId]);

  // Realtime listener: any INSERT, add if it involves this user
  useEffect(() => {
    if (!currentUserId) return;

    console.log("üëÇ Listening for realtime messages involving:", currentUserId);

    const channel = supabase
      .channel(`dm_stream_user_${currentUserId}`)
      .on(
        "postgres_changes",
        {
          event: "INSERT",
          schema: "public",
          table: "dm_messages",
        },
        (payload) => {
          const msg = payload.new as any;
          // Only show if I'm sender or recipient (RLS already restricts delivery)
          if (msg.sender_id === currentUserId || msg.recipient_id === currentUserId) {
            setMessages((prev) => {
              if (prev.some((m) => m.id === msg.id)) return prev;
              const next = [...prev, msg].sort(
                (a, b) => new Date(a.created_at).getTime() - new Date(b.created_at).getTime()
              );
              return next;
            });
            // auto-scroll
            setTimeout(() => chatEndRef.current?.scrollIntoView({ behavior: "smooth" }), 0);
          }
        }
      )
      .subscribe((status) => console.log("üì° Channel status:", status));

    return () => {
      console.log("üßπ Unsubscribing...");
      supabase.removeChannel(channel);
    };
  }, [currentUserId]);

  // Auto-scroll on initial/updates
  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleAttach = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const newFiles = Array.from(e.target.files);
      setAttachments((prev) => [...prev, ...newFiles]);
    }
  };

  // Send (user ‚Üí admin)
  const handleSend = async () => {
    if (!message.trim() || !currentUserId) return;

    setSending(true);
    try {
      const { data: session } = await supabase.auth.getSession();
      const sender = session?.session?.user;
      if (!sender) throw new Error("Not logged in");

      console.log("üßæ Insert payload:", {
        sender_id: sender.id,
        message,
        recipient_id: ADMIN_ID,
      });

      const { data, error } = await supabase
        .from("dm_messages")
        .insert([
          {
            sender_id: sender.id,
            message,
            recipient_id: ADMIN_ID,
          },
        ])
        .select();

      if (error) console.error("üî• Supabase insert error:", error);

      if (data && data[0]) {
        // Optimistic append at bottom
        setMessages((prev) => [...prev, data[0]]);
        setMessage("");
        setAttachments([]);
        setTimeout(() => chatEndRef.current?.scrollIntoView({ behavior: "smooth" }), 0);
      }
    } catch (err: any) {
      console.error("‚ùå Send failed:", err.message);
    } finally {
      setSending(false);
    }
  };

  // UI
  return (
    <div style={{ display: "flex", flexDirection: "column", height: "100vh", background: "#f9fafb", color: "#111827" }}>
      {/* Current user id */}
      <div style={{ background: "#e2e8f0", color: "#1e293b", fontSize: "12px", padding: "6px 12px", borderBottom: "1px solid #cbd5e1" }}>
        üë§ <strong>Current User ID:</strong> {currentUserId ?? "Loading‚Ä¶ (no session yet)"}
      </div>

      <div style={{ flexGrow: 1, overflowY: "auto", padding: "20px", paddingBottom: "100px" }}>
        {messages.map((msg) => {
          const isOutbound = msg.sender_id === currentUserId;
          return (
            <div key={msg.id} style={{ display: "flex", justifyContent: isOutbound ? "flex-end" : "flex-start", marginBottom: "10px" }}>
              <div
                style={{
                  background: isOutbound ? "#00838F" : "#E2E8F0",
                  color: isOutbound ? "white" : "#1E293B",
                  borderRadius: "12px",
                  padding: "10px 14px",
                  maxWidth: "70%",
                  wordBreak: "break-word",
                }}
              >
                <div style={{ fontSize: "14px" }}>{msg.message}</div>
                <div style={{ fontSize: "11px", marginTop: "4px", opacity: 0.6, textAlign: isOutbound ? "right" : "left" }}>
                  {new Date(msg.created_at).toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" })}
                </div>
              </div>
            </div>
          );
        })}
        <div ref={chatEndRef} />
      </div>

      {/* Sticky Footer */}
      <div
        style={{
          position: "sticky",
          bottom: 0,
          background: "#fff",
          borderTop: "1px solid #e5e7eb",
          padding: "12px 20px",
          display: "flex",
          alignItems: "center",
          gap: "10px",
        }}
      >
        <button onClick={() => fileInputRef.current?.click()} style={{ background: "none", border: "none", color: "#0369a1", cursor: "pointer" }}>
          <FiPaperclip size={18} />
        </button>

        <input type="file" ref={fileInputRef} multiple onChange={handleAttach} style={{ display: "none" }} />

        <input
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          placeholder="Type your message..."
          style={{ flexGrow: 1, borderRadius: "8px", border: "1px solid #d1d5db", padding: "10px", fontSize: "14px" }}
          onKeyDown={(e) => {
            if (e.key === "Enter" && !e.shiftKey) {
              e.preventDefault();
              handleSend();
            }
          }}
        />

        <button
          onClick={handleSend}
          disabled={sending}
          style={{
            background: "#00838F",
            color: "white",
            border: "none",
            borderRadius: "8px",
            padding: "10px 14px",
            fontWeight: 500,
            cursor: "pointer",
            opacity: sending ? 0.8 : 1,
            display: "flex",
            alignItems: "center",
            gap: "6px",
          }}
        >
          <FiSend size={18} />
        </button>
      </div>
    </div>
  );
};
